.container__link{
    list-style: none;
    display: block;
    height: 35px;
    width: 200px;
    margin: 5px;
    border-radius: 4px;
    transition: all 1000ms;
}

.container__link a{
    font-size: 2.0rem;
    display: block;
    text-align: center;
    padding: 5px;
    font-family: 'Handlee';
    font-weight: 500;
    color: color-letra-oscura;
    text-decoration: none;
    text-shadow: difuminado-oscuro 0px 0px 5px;
}

.container__link:hover {
    opacity: 0.7;
    background: linear-gradient(0deg, rgba(250,250,250,0.3) 0%,  rgba(250,250,250,0) 30% , rgba(250,250,250,0) 70%  ,  rgba(250,250,250,0.3) 100% );
}